import axios from '../axios.js';
/**
 * project
 * @param {*} data
 * @returns
 */
export const addProject = data => {
    return axios({
        url: '/api/projects/add',
        method: 'post',
        data
    })
}

/**
 * 
 * @param {*} data
 * @returns
 */
export const getProjectList = data => {
    return axios({
        url: '/api/projects/get',
        method: 'get',
        data
    })
}