import sourceMap from 'source-map-js';
import { Message } from 'element-ui';

// .jsfileName
function matchStr(str) {
  if (str.endsWith('.js')) return str.substring(str.lastIndexOf('/') + 1);
}

// 
function repalceAll(str) {
  return str.replace(new RegExp(' ', 'gm'), '&nbsp;');
}

function loadSourceMap(fileName, apikey, mapVersion) {
  let file = matchStr(fileName);
  if (!file) return;
  return new Promise((resolve) => {
    fetch(`http://${process.env.VUE_APP_NODE_HOST}:${process.env.VUE_APP_NODE_PORT}/api/getmap?fileName=${file}&apikey=${apikey}&mapVersion=${mapVersion}`).then((response) => {
      resolve(response.json());
    });
  });
}

export const findCodeBySourceMap = async ({ fileName, apikey, mapVersion, line, column }, callback) => {
  console.log('fileName', fileName, apikey, mapVersion);
  let sourceData = await loadSourceMap(fileName, apikey, mapVersion);
  if (!sourceData) return;
  let { sourcesContent, sources } = sourceData;
  let consumer = await new sourceMap.SourceMapConsumer(sourceData);
  let result = consumer.originalPositionFor({
    line: Number(line),
    column: Number(column)
  });
  /**
   * result
   * {
   *   "source": "webpack://myapp/src/views/HomeView.vue",
   *   "line": 24,  // 
   *   "column": 0, // 
   *   "name": null
   * }
   * */
  if (result.source && result.source.includes('node_modules')) {
    // map
    // echart webpack://web-see/node_modules/.pnpm/echarts@5.4.1/node_modules/echarts/lib/util/model.js
    return Message({
      type: 'error',
      duration: 5000,
      message: `:  ${result.source}`
    });
  }

  let index = sources.indexOf(result.source);

  // sources /./  /
  // /./ webpack://web-see/./src/main.js
  if (index === -1) {
    let copySources = JSON.parse(JSON.stringify(sources)).map((item) =>
      item.replace(/\/.\//g, '/')
    );
    index = copySources.indexOf(result.source);
  }
  console.log('index', index);
  if (index === -1) {
    return Message({
      type: 'error',
      duration: 5000,
      message: ``
    });
  }
  let code = sourcesContent[index];
  let codeList = code.split('\n');
  var row = result.line,
    len = codeList.length - 1;
  var start = row - 5 >= 0 ? row - 5 : 0, // 
    end = start + 9 >= len ? len : start + 9; // 10
  let newLines = [];
  let j = 0;
  for (var i = start; i <= end; i++) {
    j++;
    newLines.push(
      `<div class="code-line ${i + 1 == row ? 'heightlight' : ''}" title="${
        i + 1 == row ? result.source : ''
      }">${j}. ${repalceAll(codeList[i])}</div>`
    );
  }

  let innerHTML = `<div class="errdetail"><div class="errheader">${result.source} at line ${
    result.column
  }:${row}</div><div class="errdetail">${newLines.join('')}</div></div>`;
  callback(innerHTML);
};
